
import { defineComponent } from 'vue';
import {useRouter} from 'vue-router';
import Header from '@/components/Header.vue';


export default defineComponent({
  name: 'Home',
  components: {
    Header,
  },
  setup() {
      const router = useRouter();
      let gotoRouter = (path:string)=>{
          router.push(path);
      }
      return {
          gotoRouter
      }

  }
});
